/**
 * PDF Parser for Pick Uploads v1.0.0
 * Extracts text from PDF files using pdf.js
 * Parses extracted text using the existing PickParser
 */

(function() {
    'use strict';

    // pdf.js library URL (Mozilla's official CDN)
    const PDFJS_CDN = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/3.11.174/pdf.min.js';
    const PDFJS_WORKER_CDN = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/3.11.174/pdf.worker.min.js';

    let pdfjsLoaded = false;

    class PDFParser {
        constructor() {
            this.isProcessing = false;
        }

        /**
         * Ensure pdf.js library is loaded
         */
        async ensureLibraryLoaded() {
            if (pdfjsLoaded && window.pdfjsLib) {
                return true;
            }

            return new Promise((resolve, reject) => {
                const script = document.createElement('script');
                script.src = PDFJS_CDN;
                script.onload = () => {
                    // Configure worker
                    window.pdfjsLib.GlobalWorkerOptions.workerSrc = PDFJS_WORKER_CDN;
                    pdfjsLoaded = true;
                    console.log('pdf.js library loaded');
                    resolve(true);
                };
                script.onerror = () => {
                    reject(new Error('Failed to load pdf.js library'));
                };
                document.head.appendChild(script);
            });
        }

        /**
         * Extract text from a PDF file
         * @param {File} file - PDF file object
         * @returns {Promise<string>} - Extracted text
         */
        async extractText(file) {
            if (!file || file.type !== 'application/pdf') {
                throw new Error('Invalid PDF file');
            }

            await this.ensureLibraryLoaded();

            this.isProcessing = true;

            try {
                // Read file as ArrayBuffer
                const arrayBuffer = await this._readFileAsArrayBuffer(file);

                // Load PDF document
                const pdf = await window.pdfjsLib.getDocument({ data: arrayBuffer }).promise;
                
                console.log(`PDF loaded: ${pdf.numPages} pages`);

                // Extract text from all pages
                let fullText = '';
                
                for (let pageNum = 1; pageNum <= pdf.numPages; pageNum++) {
                    const page = await pdf.getPage(pageNum);
                    const textContent = await page.getTextContent();
                    
                    // Concatenate text items
                    const pageText = textContent.items
                        .map(item => item.str)
                        .join(' ');
                    
                    fullText += pageText + '\n\n';
                }

                return fullText.trim();

            } finally {
                this.isProcessing = false;
            }
        }

        /**
         * Extract and parse picks from PDF
         * @param {File} file - PDF file object
         * @returns {Promise<Array>} - Parsed picks
         */
        async extractAndParsePicks(file) {
            const text = await this.extractText(file);

            if (!text) {
                return { picks: [], rawText: '', error: 'No text extracted from PDF' };
            }

            console.log('Extracted PDF text:', text.substring(0, 500) + '...');

            // Use existing PickParser if available
            if (window.PickParser) {
                const picks = window.PickParser.parseText(text);
                return { picks, rawText: text };
            }

            // Basic parsing fallback
            return { picks: this._basicParsing(text), rawText: text };
        }

        /**
         * Basic pick parsing fallback if PickParser not available
         */
        _basicParsing(text) {
            const picks = [];
            const lines = text.split('\n').filter(l => l.trim());

            // Look for common patterns in sportsbook bet slips
            // Pattern: Team Name +/-Spread or Over/Under
            const spreadPattern = /([A-Za-z\s]+)\s+([+-]?\d+\.?\d*)/g;
            const ouPattern = /(over|under)\s+(\d+\.?\d*)/gi;
            const mlPattern = /([A-Za-z\s]+)\s+(ML|moneyline)\s*([+-]\d+)?/gi;

            lines.forEach(line => {
                let match;
                
                // Check for spread picks
                while ((match = spreadPattern.exec(line)) !== null) {
                    const team = match[1].trim();
                    const spread = parseFloat(match[2]);
                    
                    if (team.length > 2 && !isNaN(spread)) {
                        picks.push({
                            team,
                            market: 'spread',
                            line: spread,
                            rawLine: line
                        });
                    }
                }

                // Check for O/U picks
                while ((match = ouPattern.exec(line)) !== null) {
                    picks.push({
                        market: match[1].toLowerCase(),
                        line: parseFloat(match[2]),
                        rawLine: line
                    });
                }
            });

            return picks;
        }

        /**
         * Read file as ArrayBuffer
         */
        _readFileAsArrayBuffer(file) {
            return new Promise((resolve, reject) => {
                const reader = new FileReader();
                reader.onload = () => resolve(reader.result);
                reader.onerror = () => reject(reader.error);
                reader.readAsArrayBuffer(file);
            });
        }

        /**
         * Check if file is a PDF
         */
        isPDF(file) {
            return file && (
                file.type === 'application/pdf' ||
                file.name.toLowerCase().endsWith('.pdf')
            );
        }
    }

    // Create singleton instance
    window.PDFParser = new PDFParser();

})();


/* === assets/js/features/image-ocr-parser.js === */
/**
 * Image OCR Parser v1.0.0
 * Extracts text from images using Azure Computer Vision or Tesseract.js fallback
 * 
 * Supports: PNG, JPG, JPEG, GIF, BMP, WEBP
 */

(function() {
    'use strict';

    // Azure Computer Vision endpoint (configure in APP_CONFIG)
    const getAzureEndpoint = () => {
        const base = window.APP_CONFIG?.AZURE_VISION_ENDPOINT || 
                     window.APP_CONFIG?.FUNCTIONS_BASE_URL;
        return base ? `${base}/api/ocr` : '/api/ocr';
    };

    // Tesseract.js CDN for fallback
    const TESSERACT_CDN = 'https://cdn.jsdelivr.net/npm/tesseract.js@4/dist/tesseract.min.js';

    let tesseractLoaded = false;

    class ImageOCRParser {
        constructor() {
            this.isProcessing = false;
            this.useAzure = true; // Prefer Azure, fallback to Tesseract
        }

        /**
         * Extract text from an image file
         * @param {File} file - Image file object
         * @returns {Promise<Object>} - { text, confidence, method }
         */
        async extractText(file) {
            if (!this.isImage(file)) {
                throw new Error('Invalid image file');
            }

            this.isProcessing = true;

            try {
                // Try Azure Computer Vision first
                if (this.useAzure) {
                    try {
                        return await this._extractWithAzure(file);
                    } catch (azureError) {
                        console.warn('Azure OCR failed, falling back to Tesseract:', azureError);
                    }
                }

                // Fallback to Tesseract.js (client-side)
                return await this._extractWithTesseract(file);

            } finally {
                this.isProcessing = false;
            }
        }

        /**
         * Extract text using Azure Computer Vision
         */
        async _extractWithAzure(file) {
            const formData = new FormData();
            formData.append('image', file);

            const response = await fetch(getAzureEndpoint(), {
                method: 'POST',
                body: formData
            });

            if (!response.ok) {
                throw new Error(`Azure OCR failed: ${response.status}`);
            }

            const result = await response.json();

            return {
                text: result.text || '',
                confidence: result.confidence || 0,
                method: 'azure',
                regions: result.regions || []
            };
        }

        /**
         * Extract text using Tesseract.js (client-side OCR)
         */
        async _extractWithTesseract(file) {
            await this._ensureTesseractLoaded();

            // Show progress indicator
            this._showProgress('Analyzing image...');

            try {
                const imageUrl = URL.createObjectURL(file);

                const result = await Tesseract.recognize(imageUrl, 'eng', {
                    logger: (m) => {
                        if (m.status === 'recognizing text') {
                            const pct = Math.round(m.progress * 100);
                            this._showProgress(`Extracting text... ${pct}%`);
                        }
                    }
                });

                URL.revokeObjectURL(imageUrl);

                return {
                    text: result.data.text || '',
                    confidence: result.data.confidence || 0,
                    method: 'tesseract',
                    words: result.data.words || []
                };

            } finally {
                this._hideProgress();
            }
        }

        /**
         * Ensure Tesseract.js is loaded
         */
        async _ensureTesseractLoaded() {
            if (tesseractLoaded && window.Tesseract) {
                return true;
            }

            return new Promise((resolve, reject) => {
                const script = document.createElement('script');
                script.src = TESSERACT_CDN;
                script.onload = () => {
                    tesseractLoaded = true;
                    console.log('Tesseract.js loaded');
                    resolve(true);
                };
                script.onerror = () => {
                    reject(new Error('Failed to load Tesseract.js'));
                };
                document.head.appendChild(script);
            });
        }

        /**
         * Extract and parse picks from image
         * @param {File} file - Image file object
         * @returns {Promise<Object>} - { picks, rawText, method }
         */
        async extractAndParsePicks(file) {
            const result = await this.extractText(file);

            if (!result.text) {
                return { 
                    picks: [], 
                    rawText: '', 
                    error: 'No text extracted from image',
                    method: result.method 
                };
            }

            console.log(`OCR extracted (${result.method}):`, result.text.substring(0, 300) + '...');

            // Use existing PickParser if available
            if (window.PickParser) {
                const picks = window.PickParser.parseText(result.text);
                return { 
                    picks, 
                    rawText: result.text, 
                    confidence: result.confidence,
                    method: result.method 
                };
            }

            return { 
                picks: [], 
                rawText: result.text,
                confidence: result.confidence,
                method: result.method 
            };
        }

        /**
         * Check if file is an image
         */
        isImage(file) {
            if (!file) return false;
            
            const imageTypes = [
                'image/png', 
                'image/jpeg', 
                'image/jpg', 
                'image/gif', 
                'image/bmp', 
                'image/webp'
            ];
            
            return imageTypes.includes(file.type) || 
                   /\.(png|jpe?g|gif|bmp|webp)$/i.test(file.name);
        }

        /**
         * Show OCR progress indicator
         */
        _showProgress(message) {
            let indicator = document.getElementById('ocr-progress-indicator');
            
            if (!indicator) {
                indicator = document.createElement('div');
                indicator.id = 'ocr-progress-indicator';
                indicator.style.cssText = `
                    position: fixed;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    background: rgba(0, 0, 0, 0.85);
                    color: #fff;
                    padding: 24px 40px;
                    border-radius: 12px;
                    font-size: 16px;
                    z-index: 10001;
                    text-align: center;
                    box-shadow: 0 4px 20px rgba(0,0,0,0.5);
                `;
                document.body.appendChild(indicator);
            }

            indicator.innerHTML = `
                <div style="margin-bottom: 10px;">📷 Image OCR</div>
                <div>${message}</div>
            `;
            indicator.style.display = 'block';
        }

        /**
         * Hide OCR progress indicator
         */
        _hideProgress() {
            const indicator = document.getElementById('ocr-progress-indicator');
            if (indicator) {
                indicator.style.display = 'none';
            }
        }
    }

    // Create singleton instance
    window.ImageOCRParser = new ImageOCRParser();

})();


